@font-face {
  font-family: 'fontello';
  src: url('../font/fontello.eot?52328800');
  src: url('../font/fontello.eot?52328800#iefix') format('embedded-opentype'),
       url('../font/fontello.woff2?52328800') format('woff2'),
       url('../font/fontello.woff?52328800') format('woff'),
       url('../font/fontello.ttf?52328800') format('truetype'),
       url('../font/fontello.svg?52328800#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
